<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: '个人资料'
		}"></common-head>

		<ul class="setting_list">
			<li class="head_img">
				<label>头像</label>				
				<i class="iconfont icon-right"></i>
				<span>
					<img v-if="!headUrl" src="../assets/headUrl.jpg" id="uploadImg">
            		<img v-else :src="headUrl" id="uploadImg">
					<!--<button id="uploadImg"></button>-->
				</span>
			</li>
			<li>
				<label>手机</label>
				<span>{{telephone}}</span>
			</li>
			<li>
				<router-link to="/modifyPassword">
					<label>修改密码</label>				
					<i class="iconfont icon-right"></i>
					<span>********</span>
				</router-link>
			</li>
		</ul>

		<div v-show="showProgress" class="progress">
			<div class="percent">
				<span>{{percent}}</span>%
				<p>正在上传,请稍候</p>
			</div>
		</div>

	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {msgTip} from '../plugins/util'
import {getUserInfo, getAccessInfo, postHeadUrl, ajax} from '../data/service'
import {plupload} from '../plugins/plupload/plupload.full.min'

export default{
	data(){
		return{
			headUrl: '',
			telephone: '',
			headerImg: '',
			accessid: '',
			host: '',
			policy: '',
			signature: '',
			expire: '',
			dir:'',
			showProgress: false,
			percent: 0,
			newHeadUrl: '',
		}
	},
	created(){
		this.getUserBase()
	},
	mounted(){
		var that = this
		setTimeout(function(){
			that.upload()
		},1000)
    },
	mixins:[ajax],
	methods:{
		async getUserBase(){
			this.ajax(await getUserInfo(), function(resp){
				this.headUrl = resp.headUrl
				this.telephone = resp.phoneBind
			})
		},
		async authorization(){
			this.ajax(await getAccessInfo(), function(resp){
				this.accessid = resp.accessid
				this.host = resp.host
				this.policy = resp.policy
				this.signature = resp.signature
				this.expire = resp.expire
				this.dir = resp.dir
			})
		},
		async noticeHeadUrl(){
			this.ajax(await postHeadUrl(this.newHeadUrl), function(resp){
				this.headUrl = resp.headerImgSrc
			})
		},
		upload(){
			var that = this; 

			let uploader = new plupload.Uploader({
		        browse_button: 'uploadImg', //触发文件选择对话框的按钮，为那个元素id
		        //url: 'http://imgs.571xz.net', //服务器端的上传页面地址
		        url: 'http://oss.aliyuncs.com',
		        //flash_swf_url : 'js/Moxie.swf', //swf文件，当需要使用swf方式进行上传时需要配置该参数
        		//silverlight_xap_url : 'js/Moxie.xap', //silverlight文件，当需要使用silverlight方式进行上传时需要配置该参数
		        resize: { //压缩图片
					width: 100,
					height: 100,
					crop: true,
					quality: 60,
					preserve_headers: false
				},
				filters: {
					/*mime_types: [ //只允许上传图片
						{ title: "Image files", extensions : "jpg,gif,png" }
					],*/
					max_file_size: '4mb', //最大只能上传
				},

				init: {
					PostInit: function(up){						
					},

					FilesAdded: function(up, files){
						if(!that.expire || expire < (+new Date / 1000) + 3){
							that.authorization()
						}

						setTimeout(function(){
							uploader.setOption({
			                    'url': that.host,
			                    'multipart_params': {
			                        'key' : that.dir + files[0].name,
			                        'policy': that.policy,
			                        'OSSAccessKeyId': that.accessid, 
			                        'success_action_status' : '200', //让服务端返回200,不然，默认会返回204
			                        'signature': that.signature
			                    }
			                });
						},300)


		                uploader.start();
					},

					UploadProgress: function(up, file){
						this.showProgress = true
						this.percent = file.percent
					},

					FileUploaded: function(up, file, info){
						console.log(info)
		    			if(info.status == 200){
		    				that.newHeadUrl = file.name
		    				that.noticeHeadUrl()
		    				//console.log('请求到后端告诉哪张图片' + file.name)
		    			}else{
		    				msgTip(info.response)
		    			}
		    			this.showProgress = false
					},
				}
		    }); 

			uploader.init()
		},
		
	},
	components:{
		commonHead
	}
}
</script>

<style scoped>
#uploadImg{position:absolute; right:0; top:0; width:.78rem; height:.78rem; background:#ccc;}
.setting_list{padding-top:.48rem;}
.setting_list li{margin-top:.1rem; background:#fff; font-size:.16rem; color:#666; padding:.12rem .16rem; overflow:hidden;}
.setting_list li label{width:;}
.setting_list li span{float:right; margin-right:.12rem; margin-top:.03rem;}
.setting_list li i{float:right; color:#cdced2; margin-top:.04rem;}
.setting_list li a{color:#666;}
.setting_list li.head_img label{display:inline-block; padding-top:.3rem;}
.setting_list li.head_img span{position:relative; margin-top:0; display:inline-block; width:.78rem; height:.78rem; overflow:hidden;}
.setting_list li.head_img span input{width:.78rem; height:.78rem; background:#fff; position:absolute; top:0; left:0; opacity:0;}
.setting_list li.head_img img{width:.78rem; height:.78rem; border-radius:50% 50%; pointer-events:none;}
.setting_list li.head_img i{margin-top:.32rem;}
.progress{position:fixed; width:100%; height:100%; top:0; left:0; background:rgba(0,0,0,.2); z-index:300;}
.progress .percent{width:2rem; height:1.2rem; position:absolute; background:#fff; font-size:.14rem; color:#999; left:50%; top:50%; margin-left:-1rem; margin-top:-1rem; text-align:center; padding-top:.2rem;}
.progress .percent span{font-size:.3rem; color:#f40; margin-right:.08rem;}
.progress .percent p{padding-top:.2rem;}
</style>


